import React, { Component } from 'react';
import './home.css';
import HYTabView, { TabItem } from '../../components/HYTabView';
import HomeContentView from './HomeContentView';
import AbountContentView from './AboutContentView';
import BottomView from './BottomView';

const tabHome = new TabItem('home', '首页');
const tabAbount = new TabItem('about', '关于我们');

interface HomeState {
    selectedTab: TabItem;
}

export default class Home extends Component<any, HomeState> {
    constructor(props: any) {
        super(props);
        this.state = {
            selectedTab: tabHome
        }
    }

    p_onTabClick = (tab: TabItem) => {
        this.setState({
            selectedTab: tab
        });
    }

    render() {
        let contentDom = null;
        let { selectedTab } = this.state;
        if (selectedTab.label === tabHome.label) {
            contentDom = <HomeContentView/>;
        } else if (selectedTab.label === tabAbount.label) {
            contentDom = <AbountContentView/>;
        }

        return (
            <div>
                <div className='hy-header'>
                    <div className='logo'>
                        <img src={require('../../images/logo/logo_home.png')} alt='logo' />
                    </div>
                    <HYTabView
                        items={[tabHome, tabAbount]}
                        onItemClick={this.p_onTabClick}
                    />
                </div>
                <div>{contentDom}</div>
                <BottomView/>
            </div>
        )
    }
}