import React, { Component } from 'react';
import './button.css';


export interface ButtonProps {
    tag?: number;    // 绑定tag
    title?: string;      // 标题
    onButtonClick?: ((tag: number) => void);  // 回调
}

export default class HYButton extends Component<ButtonProps, {}> {
    constructor(props: ButtonProps) {
        super(props);
    }

    p_onButtonClick = (e: any) => {
        let cb = this.props.onButtonClick;
        if (cb) {
            let tag = this.props.tag || 0;
            cb(tag);
        }
    }

    render() {
        let title = this.props.title || "Button";
        return (
            <div className='hy-button' onClick={this.p_onButtonClick}>
                {title}
            </div>
        )
    }
}