import React, { Component } from 'react';
import './tabview.css';

export class TabItem {
    label: string;
    value: string;

    constructor(label: string, value: string) {
        this.label = label;
        this.value = value;
    }
}

export interface TabViewProps {
    items: TabItem[];   // 所有标签项
    selectedItem?: TabItem;     // 被选中的，默认 第一个
    onItemClick: ((tab: TabItem) => void);  // 回调
}

class TabState {
    selectedItem: TabItem;

    constructor(item: TabItem) {
        this.selectedItem = item;
    }
}

export default class HYTabView extends Component<TabViewProps, TabState> {
    constructor(props: TabViewProps) {
        super(props);
        // 保存已选中的Tab
        let selItem = this.props.selectedItem || this.props.items[0];
        this.state = {
            selectedItem: selItem,
        }
    }

    p_onItemClick = (tab: TabItem) => {
        let { selectedItem } = this.state;
        if (selectedItem.label === tab.label) {
            // 已经选中
            // console.log('selected do nothing');
        } else {
            this.setState({
                selectedItem: tab
            });

            this.props.onItemClick(tab);
        }
    }

    p_getTabItem = (tabItem: TabItem, selItem: TabItem) => {
        let line = null;
        let clsName = 'hy-tab-item';
        if (tabItem.label === selItem.label) {
            line = <div className='hy-tab-item-sel'></div>;
            clsName = 'hy-tab-item-selected';
        }
        return (
            <div className={clsName} key={tabItem.label} onClick={e => this.p_onItemClick(tabItem)}>
                <div>{tabItem.value}</div>
                {line}
            </div>
        )
    }

    render() {
        let doms = [];
        let tabItems = this.props.items;
        let selItem = this.state.selectedItem;
        for (let item of tabItems) {
            doms.push(this.p_getTabItem(item, selItem));
        }
        return (
            <div className='hy-tab'>
                {doms}
            </div>
        )
    }
}